import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import Speed from '../../components/data/speed';
import Elevation from '../../components/data/elevation';
import Weight from '../../components/data/weight';
import Distance from '../../components/data/distance';
import Temperature from '../../components/data/temperature';

import mu from '../../lib/musher-util';

@withText((props) => ({
    week: <Text id='workout.graph.week' />,
    distance: <Text id='workout.graph.distance' />,
    ascend: <Text id='workout.graph.ascend' />,
    plan: <Text id='workout.graph.plan' />,
}))
@observer
class WorkoutTable extends Component {
    render() {
        const { userStore } = this.props.stores;

        const currentMonth = new Date().getMonth() + 1;
        const currentYear = new Date().getFullYear();

        const { isLoading, year = currentYear, month = currentMonth, showHeader = true } = this.props;
		const seasonStart = parseInt(month < 8 ? year - 1 : year, 10);

        const { user } = userStore;
        const { language = 'en' } = user;
		const MONTHS = mu.getMonths(language);


        // const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(year, monthNum) || {};
        const seasonMonths = util.monthRange(`${seasonStart}-08-01`, `${seasonStart + 1}-07-31`);
        const prevSeasonMonths = util.monthRange(`${seasonStart - 1}-08-01`, `${seasonStart}-07-31`);
        const seasonWeeks = util.weekRange(`${seasonStart}-08-01`, `${seasonStart + 1}-07-31`);

        const xTicksAll = seasonWeeks.map(wo => wo.week);
        const parts = xTicksAll.length % 6;
        const partSize = Math.floor(xTicksAll.length / parts);
        const xTicks = [];
        for (let i = 0, l = xTicksAll.length; i < l; i += partSize) {
            xTicks.push(xTicksAll[i]);
        }

        return (
            <>
                {showHeader && <h5><Text id='workout.graph.title'>Treningsstatistikk</Text> {seasonStart} - {seasonStart + 1}</h5>}
                <div class='w-100 mt-3 mb-4'>
                    {/* <xmp>graphWorkoutSummaryWeekDistanceCurrent: {JSON.stringify(graphWorkoutSummaryWeekDistanceCurrent, null, 4)}</xmp>
                    <xmp>graphWorkoutSummaryWeekDistancePrev: {JSON.stringify(graphWorkoutSummaryWeekDistancePrev, null, 4)}</xmp> */}
                    <div class='table-responsive-lg'>
                        <table class='table table-sm table-striped font-weigth-lighter border-bottom'>
                            <thead>
                                <tr>
                                    <th class='text-center'><Text id='workout.graph.season'>Sesong</Text></th>
                                    {seasonMonths.map(m => (
                                        <th class='text-center'><a href={`/workouts/month/${m.month}/${m.year}`}>{MONTHS[m.month - 1]}</a></th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <th scope='row' style='padding: .15rem;'><nobr>{seasonStart - 1} - {seasonStart}</nobr></th>
                                    {prevSeasonMonths.map(m => {
                                        // const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(m.year, m.month) || {};
                                        return (
                                            <>
                                                <td class='text-right px-3'>
                                                    {/* <nobr>{summaryMonth.distance}</nobr><br /> */}
                                                    <nobr>
                                                        <Distance stores={this.props.stores} value={userStore.findWorkoutSummaryMonth(m.year, m.month)} smallUnit={true} />
                                                    </nobr><br />
                                                    <nobr>
                                                        <Elevation stores={this.props.stores} value={userStore.findWorkoutSummaryMonth(m.year, m.month, 'elevation')} smallUnit={true} />
                                                    </nobr><br />
                                                </td>
                                            </>
                                        );
                                    })}
                                </tr>
                                <tr>
                                    <th scope='row' style='padding: .15rem;'><nobr>{seasonStart} - {seasonStart + 1}</nobr></th>
                                    {seasonMonths.map(m => {
                                        // const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(m.year, m.month) || {};
                                        return (
                                            <>
                                                <td class='text-right px-3'>
                                                    <nobr>
                                                        <Distance stores={this.props.stores} value={userStore.findWorkoutSummaryMonth(m.year, m.month)} smallUnit={true} />
                                                    </nobr><br />
                                                    <nobr>
                                                        <Elevation stores={this.props.stores} value={userStore.findWorkoutSummaryMonth(m.year, m.month, 'elevation')} smallUnit={true} />
                                                    </nobr><br />
                                                </td>
                                            </>
                                        );
                                    })}
                                </tr>
                            </tbody>
                        </table>
                    </div>

                </div>
            </>
        );
    }
}

export default WorkoutTable;
